@import url(https://fonts.googleapis.com/css2?family=Montserrat:wght@300;600;900&display=swap);
body {
  background-color: #334756;
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

header {
  background-color: #2F86A6;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  font-family: 'Montserrat', sans-serif;
}

header .logo {
  color: #fff;
  text-align: center;
  font-size: 30px;
}

.botones{
  display:flex;
  justify-content: space-between;
 
}

.botones button {  
  margin-left: 5rem;
  border-radius: 2rem;
  font-family: 'Montserrat', sans-serif;
  width: 100%;
  padding: 5px;
  border: none;
  margin-bottom: 4px;
  box-sizing: border-box;
  font-size: 1rem;
  text-align: center;
  background: whitesmoke;
  color: #000;
  cursor: pointer;
  
}

.todo-card {
  background-color: #EDEDED;
  width: 22%;
  margin-top: 20px;
  border: 1px solid #000;
  border-radius: 10px;
  padding: 10px;
  height: 130px;
  transition: all 0.3s ease;
  box-shadow: 10px 10px 5px 0px rgba(0, 0, 0, 0.15);
  -webkit-box-shadow: 10px 10px 5px 0px rgba(0, 0, 0, 0.15);
  -moz-box-shadow: 10px 10px 5px 0px rgba(0, 0, 0, 0.15);
}

.todo-card:hover {
  -webkit-transform: translateY(-10px);
          transform: translateY(-10px);
}

.todo-title {
  font-size: 1rem;
}

.todo-actions .complete {
  color: #fff;
  background-color: #d57e7e;
  padding: 10px 15px;
  font-size: 1rem;
  outline: none;
  border: 1px solid #d57e7e;
  border-radius: 10px;
}

.todo-actions .reset {
  color: #fff;
  background-color: #c6d57e;
  padding: 5px 10px;
  font-size: 1rem;
  outline: none;
  border: 1px solid #c6d57e;
  border-radius: 10px;
}

.loader {
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 2rem;
}

.todo-container {
  width: 90%;
  margin: auto;
  display: flex;
  justify-content: space-between;
  align-items: center;
  flex-wrap: wrap;
}

